@charset "UTF-8";
.text { margin: 52px 0; }

.text h1, .text h2, .text h3, .text h4, .text h5, .text h6 { margin-bottom: 12px; }

.text strong, .text b { display: block; margin-bottom: 6px; }

.text a { text-decoration: none; white-space: nowrap; color: #000; position: relative; font-family: 'Hind Madurai', Arial, Helvetica, sans-serif; font-weight: 600; }

.text a:before { content: ""; position: absolute; bottom: 3px; left: 0; width: 100%; height: 1px; background: #ec6b06; }

.text p { font-family: 'Hind Madurai', Arial, Helvetica, sans-serif; font-weight: 300; font-size: 18px; line-height: 1.6; margin-bottom: 8px; }

@media only screen and (max-width: 719px) { .text p { font-size: 16px; line-height: 1.625; margin-bottom: 6px; } }

.text p strong, .text p b { display: inline; margin-bottom: 0; }

.text ul li { position: relative; padding-left: 22px; font-family: 'Hind Madurai', Arial, Helvetica, sans-serif; font-weight: 300; font-size: 18px; line-height: 1.6; margin-bottom: 8px; }

@media only screen and (max-width: 719px) { .text ul li { font-size: 16px; line-height: 1.625; margin-bottom: 6px; } }

.text ul li:before { content: '•'; position: absolute; top: 0; left: 0; }

.case { position: relative; }

.case__wrapper { position: relative; }

.case__hero-bg { -webkit-background-size: cover; background-size: cover; background-position: 50% 50%; background-repeat: no-repeat; z-index: 0; position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.case__hero-bg--portrait { display: none; }

@media only screen and (orientation: portrait) { .case__hero-bg.case__hero-bg--landscape { display: none; }
  .case__hero-bg.case__hero-bg--portrait { display: block; } }

.case__hero { padding-top: 90px; padding-bottom: 30px; position: relative; min-height: 502px; }

@media only screen and (max-width: 1279px) { .case__hero { padding-top: 88px; min-height: 499px; } }

@media only screen and (max-width: 1023px) { .case__hero { min-height: 501px; } }

@media only screen and (max-width: 767px) { .case__hero { padding-top: 89px; } }

@media only screen and (max-width: 719px) { .case__hero { padding-top: 94px; min-height: 450px; } }

.case__hero h1 { line-height: 0.95; }

@media only screen and (max-width: 1679px) { .case__hero h1 { line-height: 0.96; } }

@media only screen and (max-width: 1023px) { .case__hero h1 { font-size: 58px; line-height: 0.96; } }

@media only screen and (max-width: 599px) { .case__hero h1 { font-size: 48px; line-height: 0.935; } }

.case__hero .wrapper { position: relative; z-index: 1; }

@media only screen and (max-width: 767px) { .case__content-wrapper { order: 1; } }

@media only screen and (max-width: 767px) { .case__sidebar-wrapper { order: 0; padding-top: 70px; margin-bottom: 40px; } }

@media only screen and (max-width: 719px) { .case__sidebar-wrapper { margin-bottom: 63px; } }

@media only screen and (max-width: 599px) { .case__sidebar-wrapper { margin-bottom: 52px; } }

.case__content { padding-bottom: 208px; }

.case__sidebar { padding-top: 69px; }

@media only screen and (max-width: 1279px) { .case__sidebar { padding-top: 71px; } }

@media only screen and (max-width: 1023px) { .case__sidebar { padding-top: 0; } }

@media only screen and (max-width: 1023px) { .case__sidebar { display: flex; flex-wrap: wrap; } }

@media only screen and (max-width: 719px) { .case__sidebar { display: block; } }

.case__inner { padding-top: 70px; }

@media only screen and (max-width: 1279px) { .case__inner { padding-top: 71px; } }

@media only screen and (max-width: 1023px) { .case__inner { margin-bottom: 60px; } }

@media only screen and (max-width: 767px) { .case__inner { margin-bottom: 0px; padding-top: 0; } }

.case__title { font-size: 48px; line-height: 1.045; margin-bottom: 56px; }

@media only screen and (max-width: 767px) { .case__title { display: none; } }

.case__title_mob { display: none; }

@media only screen and (max-width: 767px) { .case__title_mob { display: block; } }

@media only screen and (max-width: 1679px) { .case__title { font-size: 36px; line-height: 1.06; } }

@media only screen and (max-width: 1279px) { .case__title { font-size: 28px; line-height: 1.08; } }

@media only screen and (max-width: 1023px) { .case__title { font-size: 36px; } }

@media only screen and (max-width: 767px) { .case__title { margin-bottom: 55px; } }

@media only screen and (max-width: 719px) { .case__title { font-size: 28px; margin-bottom: 57px; } }

@media only screen and (max-width: 599px) { .case__title { letter-spacing: -0.2px; } }

.case__btns-wrapper { left: 0; position: absolute; top: 502px; width: 100%; }

@media only screen and (max-width: 1279px) { .case__btns-wrapper { top: 499px; } }

@media only screen and (max-width: 719px) { .case__btns-wrapper { transform: none; bottom: auto; top: 0; position: sticky; top: 47px; z-index: 100; } }

@media only screen and (max-width: 719px) { .case__btns-wrapper .wrapper { padding: 0; } }

.case__btns-inner { background: #ec6b06; width: 72px; height: 196px; display: flex; flex-direction: column; align-items: flex-start; justify-content: space-between; font-size: 0; padding: 20px 8px 33px 8px; }

@media only screen and (max-width: 1279px) { .case__btns-inner { padding: 20px 10px 32px; } }

@media only screen and (max-width: 1023px) { .case__btns-inner { padding: 20px 8px 32px; } }

@media only screen and (max-width: 767px) { .case__btns-inner { height: 197px; } }

@media only screen and (max-width: 719px) { .case__btns-inner { height: 49px; width: 100%; padding: 8px 10px 8px 7.28vw; flex-direction: row; justify-content: flex-start; align-items: flex-start; } }

@media only screen and (max-width: 599px) { .case__btns-inner { padding: 8px 10px 8px 7.4vw; } }

@media only screen and (max-width: 450px) { .case__btns-inner { padding: 8px 10px 8px 7.6vw; } }

@media only screen and (max-width: 374px) { .case__btns-inner { padding: 8px 10px 8px 8.1vw; } }

.case__back, .case__download { text-decoration: none; align-items: flex-start; justify-content: flex-start; display: flex; flex-direction: column; }

.case__back span, .case__download span { font-size: 12px; font-family: 'Hind Madurai', Arial, Helvetica, sans-serif; font-weight: 600; color: #000; display: block; line-height: 1.1; }

@media only screen and (max-width: 719px) { .case__back span, .case__download span { font-size: 10px; } }

.case__back i, .case__download i { display: block; position: relative; }

.case__back { background: none; appearance: none; border: none; }

@media only screen and (max-width: 719px) { .case__back { margin-right: 35px; } }

.case__back span { margin-bottom: 6px; }

@media only screen and (max-width: 719px) { .case__back span { margin-bottom: 0px; } }

.case__back svg { fill: #fff; width: 23px; height: 12px; transition: fill .25s ease-in-out; }

@media only screen and (min-width: 1025px) { .case__back:hover svg { fill: #000; } }

@media only screen and (max-width: 719px) { .case__download { flex-direction: row; } }

.case__download span { margin-bottom: 18px; }

@media only screen and (max-width: 719px) { .case__download span { margin-right: 19px; } }

.case__download i { padding-bottom: 6px; }

@media only screen and (max-width: 719px) { .case__download i { padding-top: 1px; padding-bottom: 7px; } }

.case__download i:after { content: ''; position: absolute; height: 2px; width: 100%; background: #ffffff; left: 0; bottom: 0; }

.case__download .icon { width: 23px; height: 22px; fill: #000; transition: fill .25s ease-in-out; }

@media only screen and (min-width: 1025px) { .case__download:hover .icon { fill: #fff; } }

.case__block { margin-bottom: 20px; }

@media only screen and (max-width: 1023px) { .case__block { width: 54.7%; padding-right: 7%; }
  .case__block:nth-of-type(2n) { width: 45.3%; } }

@media only screen and (max-width: 719px) { .case__block { width: 100%; padding-right: 0; margin-bottom: 23px; }
  .case__block:nth-of-type(2n) { width: 100%; } }

.case__block-title { font-size: 18px; margin-bottom: 1px; font-family: 'Hind Madurai', Arial, Helvetica, sans-serif; font-weight: 600; }

.case__block-text { font-size: 16px; }

@media only screen and (max-width: 719px) { .case__block-text { letter-spacing: -0.1px; } }

.case .text { margin: 0; }

.case .text p { line-height: 1.4; }

@media only screen and (max-width: 1679px) { .case .text p { line-height: 1.2; margin-bottom: 12px; } }

@media only screen and (max-width: 1279px) { .case .text p { margin-bottom: 12px; } }

@media only screen and (max-width: 1023px) { .case .text p { margin-bottom: 10px; } }

@media only screen and (max-width: 599px) { .case .text p { margin-bottom: 12px; } }

.case .text strong { font-size: 22px; display: block; }

@media only screen and (max-width: 1679px) { .case .text strong { font-size: 20px; } }

@media only screen and (max-width: 599px) { .case .text strong { font-size: 18px; } }

.case .text ul:not(:last-of-type) { margin-bottom: 34px; }

.case .text ul li { line-height: 1.45; }

@media only screen and (max-width: 1279px) { .case .text ul li { padding-left: 20px; } }

@media only screen and (max-width: 1023px) { .case .text ul li { padding-left: 33px; } }

@media only screen and (max-width: 767px) { .case .text ul li { padding-left: 26px; } }

@media only screen and (max-width: 719px) { .case .text ul li { font-size: 18px; line-height: 1.5; } }

@media only screen and (max-width: 599px) { .case .text ul li { padding-left: 14px; } }

@media only screen and (max-width: 719px) { body.ie .case__btns-wrapper { position: absolute; top: 0; } }
