
@import "text";


.case {
  position: relative;
  &__wrapper {
    position: relative; }
  &__hero-bg {
      -webkit-background-size: cover;
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      z-index:  0;
      @include coverdiv;
      &--landscape {}

      &--portrait {
          display: none; }
      @media only screen and (orientation: portrait) {
        &.case__hero-bg--landscape {
          display: none; }
        &.case__hero-bg--portrait {
          display: block; } } }
  &__hero {
    padding-top: 90px;
    padding-bottom: 30px;
    position: relative;
    min-height: 502px;
    @include desktop-less-lg;
    @include desktop-less-md {
      padding-top: 88px;
      min-height: 499px; }
    @include desktop-less-sm {
      min-height: 501px; }
    @include tablet-less-md {
      padding-top: 89px; }
    @include tablet-less-sm {
      padding-top: 94px;
      min-height: 450px; }
    @include mob-less-lg;
    h1 {
      line-height: 0.95;
      @include desktop-less-lg {
        line-height:  0.96; }
      @include desktop-less-sm {
        font-size:  58px;
        line-height: 0.96; }
      @include mob-less-lg {
        font-size: 48px;
        line-height: 0.935; } }
    .wrapper {
        position: relative;
        z-index: 1; } }
  &__content-wrapper {
      @include tablet-less-md {
        order: 1; } }
  &__sidebar-wrapper {
      @include tablet-less-md {
        order: 0;
        padding-top: 70px;
        margin-bottom: 40px; }
      @include tablet-less-sm {
        margin-bottom: 63px; }
      @include mob-less-lg {
          margin-bottom: 52px; } }
  &__content {
      padding-bottom: 208px; }
  &__sidebar {
    padding-top: 69px;
    @include desktop-less-md {
      padding-top: 71px; }
    @include desktop-less-sm {
      padding-top: 0; }
    @include desktop-less-sm {
      display: flex;
      flex-wrap: wrap; }
    @include tablet-less-sm {
      display: block; } }
  &__inner {
    padding-top: 70px;
    @include desktop-less-md {
      padding-top: 71px; }
    @include desktop-less-sm {
      margin-bottom: 60px; }
    @include tablet-less-md {
      margin-bottom: 0px;
      padding-top: 0; } }
  &__title {
    font-size: 48px;
    line-height: 1.045;
    margin-bottom: 56px;
    @include tablet-less-md {
        display: none; }
    &_mob {
      display: none;
      @include tablet-less-md {
          display: block; } }
    @include desktop-less-lg {
      font-size: 36px;
      line-height: 1.06; }
    @include desktop-less-md {
      font-size: 28px;
      line-height: 1.08; }
    @include desktop-less-sm {
      font-size: 36px; }
    @include tablet-less-md {
      margin-bottom: 55px; }
    @include tablet-less-sm {
      font-size: 28px;
      margin-bottom: 57px; }
    @include mob-less-lg {
      letter-spacing: -0.2px; } }
  &__btns-wrapper {
    left: 0;
    position: absolute;
    top: 502px;
    width: 100%;
    @include desktop-less-md {
      top: 499px; }
    @include tablet-less-sm {
      transform: none;
      bottom: auto;
      top: 0;
      position: sticky;
      top: 47px;
      z-index: 100; }
    .wrapper {
      @include tablet-less-sm {
        padding: 0; } } }
  &__btns-inner {
      background: $orange;
      width: 72px;
      height: 196px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      font-size: 0;
      padding: 20px 8px 33px 8px;
      @include desktop-less-md {
         padding: 20px 10px 32px; }
      @include desktop-less-sm {
         padding: 20px 8px 32px; }
      @include tablet-less-md {
         height: 197px; }
      @include tablet-less-sm {
         height: 49px;
         width: 100%;
         padding: 8px 10px 8px 7.28vw;
         flex-direction: row;
         justify-content: flex-start;
         align-items: flex-start; }
      @include mob-less-lg {
         padding: 8px 10px 8px 7.4vw; }
      @include r(450) {
         padding: 8px 10px 8px 7.6vw; }
      @include mob-less-md {
         padding: 8px 10px 8px 8.1vw; } }

  &__back, &__download {
      text-decoration: none;
      align-items: flex-start;
      justify-content: flex-start;
      display: flex;
      flex-direction: column;
      span {
        font-size: 12px;
        @include hm-sb;
        color: #000;
        display: block;
        line-height: 1.1;
        @include tablet-less-sm {
          font-size: 10px; } }
      i {
          display: block;
          position: relative; } }
  &__back {
      background: none;
      appearance: none;
      border: none;
      @include tablet-less-sm {
          margin-right: 35px; }
      span {
          margin-bottom: 6px;
          @include tablet-less-sm {
              margin-bottom: 0px; } }
      svg {
          fill: #fff;
          width: 23px;
          height: 12px;
          transition: fill .25s ease-in-out; }
      @include rmin(1025) {
        &:hover {
            svg {
                fill: #000; } } } }

  &__download {
    @include tablet-less-sm {
      flex-direction: row; }
    span {
      margin-bottom: 18px;
      @include tablet-less-sm {
        margin-right: 19px; } }
    i {
      padding-bottom: 6px;
      @include tablet-less-sm {
        padding-top: 1px;
        padding-bottom: 7px; }
      &:after {
        content:  '';
        position: absolute;
        height: 2px;
        width: 100%;
        background: #ffffff;
        left: 0;
        bottom: 0; } }
    .icon {
      width: 23px;
      height: 22px;
      fill: #000;
      transition: fill .25s ease-in-out; }
    @include rmin(1025) {
        &:hover {
            .icon {
                fill: #fff; } } } }

  &__block {
    margin-bottom: 20px;
    @include desktop-less-sm {
      width: 54.7%;
      padding-right:  7%;
      &:nth-of-type(2n) {
        width: 45.3%; } }
    @include tablet-less-sm {
      width: 100%;
      padding-right: 0;
      margin-bottom: 23px;
      &:nth-of-type(2n) {
        width: 100%; } } }

  &__block-title {
    font-size: 18px;
    margin-bottom: 1px;
    @include hm-sb; }
  &__block-text {
    font-size: 16px;
    @include tablet-less-sm {
      letter-spacing: -0.1px; } }

  .text {
    margin: 0;
    p {
      line-height: 1.4;
      @include desktop-less-lg {
        line-height:  1.2;
        margin-bottom: 12px; }
      @include desktop-less-md {
        margin-bottom: 12px; }
      @include desktop-less-sm {
        margin-bottom: 10px; }
      @include mob-less-lg {
        margin-bottom: 12px; } }
    strong {
      font-size: 22px;
      //line-height: 1.4
      display: block;
      @include desktop-less-lg {
        font-size: 20px;
 }        //line-height:  1.2
      @include mob-less-lg {
        font-size: 18px; } }
    ul {
      &:not(:last-of-type) {
        margin-bottom: 34px; }
      li {
        line-height: 1.45;
        @include desktop-less-md {
          padding-left: 20px; }
        @include desktop-less-sm {
          padding-left: 33px; }
        @include tablet-less-md {
          padding-left: 26px; }
        //  23
        @include tablet-less-sm {
          font-size: 18px;
          line-height: 1.5; }
        @include mob-less-lg {
          padding-left: 14px; } } } } }
body {
  &.ie {
    .case {
      &__btns-wrapper {
        @include tablet-less-sm {
          position: absolute;
          top: 0; } } } } }


